<template>
   <el-row class="noteP">
     <el-col :span="24" class="top">
   	  <el-col :span="24" class="title">
   	  		<!-- <img src="../../assets/img/tag.png" > -->
   				<span class="ctitle">
   					笔记名称:{{item.className}}
   				</span>
   	  </el-col>
     </el-col>
	 <el-col class="stit" :span="24">
		 <el-col :span="5" class="stitle">
			 点击可再次编辑内容：<img src="../../../../assets/img/note@2x.png" >
		 </el-col>
		 <el-col :span="8" class="stitle" :offset="11">
		 	上次编辑时间:{{time(item.updateTime)}}
		 </el-col>
	 </el-col>
	 <el-col :span="24">
		 <el-input
		   type="textarea"
		   :rows="30"
		   placeholder="请输入内容"
		   v-model="textarea">
		 </el-input>
	 </el-col>
	 <el-col :span="24" class="subtn">
		   <el-button type="primary">确定</el-button>
		   <!-- <el-button type="info" plain>取消</el-button> -->
	 </el-col>
   </el-row>
</template>

<script>
	import {parseTime} from '../../../../utils/utils.js'
export default {
	name:'noteP',
	data(){
		return {
			textarea:'',
			item:''
		}
	},
	mounted() {
		this.item=JSON.parse(localStorage.getItem('noteItem'))
		this.textarea=this.item.noteContent
	},
	components:{
	},
	methods:{
		time(val){
			return parseTime(val)
		}
	}
}
</script>


<style scoped>
@import '../../../../assets/css/noteP.css';
</style>



